			  		<div v-show="showGrap3" id="graphCharts3" style="margin:0 auto; width: 1200px;height: 700px;text-align: center;"></div>
			    </el-tab-pane>
			    <el-tab-pane label="疑点--法规" name="four">
			    	<div v-show="!showGrap4" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
						<i class="am-icon-spinner am-icon-spin"></i> loading
					</div>
			  		<div v-show="showGrap4" id="graphCharts4" style="margin:0 auto; width: 1200px;height: 700px;text-align: center;"></div>
			    </el-tab-pane>
			</el-tabs>
			
		</el-dialog>
	    <div class="cont box-border-shadow">
	    	<div class="list-btn fl" @click="getData('空')">
	            知<br>识<br>图<br>谱
	        </div>
	    	<div class="find">
				<div style="margin-top: 15px;width: 40%;margin: 0 auto">
				  <el-input placeholder="请输入内容" v-model="findWord" class="input-with-select">
				    <el-select v-model="selectArea" slot="prepend">
				      <el-option label="财务法规" value="1"></el-option>
				      <el-option label="资环法规" value="2"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search" @click="search(findWord)"></el-button>
				  </el-input>
				</div>
                <!-- <div class="search">
                    <input type="text" autocomplete="off" v-model="findWord" id="keyword" placeholder="层次性主题搜索">
                    <button class="fr icon-search" id="sub2" @click="search"></button>
                </div> -->
            </div>
          <!--   <ul class="select">
				<li class="select-list">
					<dl id="select1">
						<dt>xxx筛选：</dt>
						<dd class="select-all selected"><a href="#">全部</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
					</dl>
				</li>
				<li class="select-list">
					<dl id="select2">
						<dt>xxx筛选：</dt>
						<dd class="select-all selected"><a href="#">全部</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
						<dd><a href="#">xxxxx</a></dd>
					</dl>
				</li>
				<li class="select-list">
					<dl id="select3">
						<dt>日期筛选：</dt>
						<dd class="select-all selected"><a href="#">全部</a></dd>
						<dd><a href="#">按年份</a></dd>
						<dd><a href="#">按季度</a></dd>
						<dd><a href="#">按月份</a></dd>
						<dd><a href="#">按周期</a></dd>
					</dl>
				</li>
				<li class="select-result">
					<dl>
						<dt>已选条件：</dt>
						<dd class="select-no">暂时没有选择过滤条件</dd>
					</dl>
				</li>
			</ul>  -->
            <el-row :gutter="20">
			  	<el-col :span="tree?12:24">
			  		<ul v-if="faguiList.length>0">
		                <li v-for="listItem in faguiList" class="item">
		                    <p class="item-head"><span class="icon-fl" v-text="selectArea=='财务法规'||selectArea=='1'?'财务法规':'资环法规'"></span> <span @click="getFaguiXq(listItem.icon)" class="title">《 {{listItem.icon}} 》</span></p>
		                    <p class="item-b"><span v-text="selectArea=='财务法规'||selectArea=='1'?'':'来源：'"></span>{{listItem.desc}} &nbsp;&nbsp;|&nbsp;&nbsp; {{listItem.time}}</p>
		                </li>
	            	</ul>
	            	<div v-else style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
						<i class="am-icon-spinner am-icon-spin"></i> loading
					</div>
	            	<div class="block">
					    <el-pagination
					      @current-change="handleCurrentChange()"
					      :current-page.sync="currentPage"
					      :page-size="10"
					      background
					      layout="prev, pager, next, jumper"
					      :total="10*pages">
					    </el-pagination>
					</div>
			  	</el-col>
			  	<el-col :span="12" v-show="tree">
				  	<div class="tree-box box-border-shadow" style="min-height: 600px;">
		            	<!-- <div style="position: absolute;cursor: pointer; z-index:10000;left: 0;top: 0;font-size: 16px;color: #333" @click="showMin=!showMin"><i :class="!showMin ? 'icon-canclefullscreen' : 'icon-fullscreen'" :title="!showMin ? '最小化' : '放大'"></i></div> -->
				        <img v-show="imgShow" class="noSjImg" src="../../../assets/no-sj.png">
				        <div v-show="!imgShow" :class="{min:showMin}" id="searchTree" ref="searchTree" style="width:100%;height: 600px;margin: 0 auto"></div>
				   	</div>
			  	</el-col>
			</el-row>
        </div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import * as echarts from 'echarts'
	import reqData from "../../../reqData.js"
	export default{
		name:'page1_2_1',
		data(){
			return{
				msg:["审计准备","审计标准及依据","法律法规"],
				currentPage: 1,
				pages:12,
				faguiList:[],
				faguiXq:{},
				showMin:false,
				dialogVisible:false,
				dialogVisible2:false,
				showGrap:false,
				showGrap2:false,
				showGrap3:false,
				showGrap4:false,
				activeName: 'first',
				findWord:'',
				selectArea:"财务法规",
				imgShow:false,
				tree:false,
				name:',',
				NodeStr:','
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			companyName(){
				return JSON.parse(this.$store.getters.getCompany)
			}
		},
		methods: {
		    optionTree(json) {
		        return {
		            tooltip: {
		                trigger: 'item',
		                triggerOn: 'mousemove'
		            },
		            textStyle:{color:"#000",fontSize:16},
		            // backgroundColor:'rgba(0, 0, 0,0.05)',
		            series: [
		                {
		                    type: 'tree',
		                    data: json,
		                    top: '14%',
		                    bottom: '14%',
		                    right:0,
		                    left:0,
		                    expandAndCollapse:false,
		                    layout: 'radial',
		                    itemStyle:{borderColor:'#c24b4b'},
		                    symbol: 'emptyCircle',
		                    symbolSize: 7,
		                    initialTreeDepth: 3,
		                    animationDurationUpdate: 750

		                }
		            ]
		         }     
		    },
		    search(word){
		    	let _this=this;
		    	_this.currentPage=1;
		    	_this.imgShow=false;
				_this.treeList=false;
				_this.tree=false;
		    	if(word===''){
		    		alert("请填写需要查询的关键词");
		    	}else{
		    		_this.NodeStr=word;
		    		_this.getListPages();
				    _this.handleCurrentChange(1);
		    		_this.tree=true;
		    		var myChart = echarts.init(document.getElementById("searchTree"));
		    		myChart.showLoading();
		    		let biaoshi=this.selectArea=="财务法规"||this.selectArea==1?1:2;
		    		reqData
				      .req({
				          apiName: "searchTree",
				          data: {             
				            name:word,
				            biaoshi:biaoshi
				          }
				      })
			          .then(res => {
			            res = res.data;
			            if (res && res.code === "200") {
			               myChart.hideLoading();
			              let data = res.data;
			              console.log(data);
			              if(data.length>0){
			              	sessionStorage.setItem('znjsTree',JSON.stringify(data));
			              	sessionStorage.setItem('znjsNode',word);
			                myChart.setOption(this.optionTree(data));
			                myChart.resize({
								width:'auto',
								height:'auto'
							})
			                $(window).resize(function(){
								myChart.resize({
									width:'auto',
									height:'auto'
								})
							});
			                myChart.on('click', function (params) {
			                  console.log(params);
			                  _this.currentPage=1;
			                  let id=this.getDom().id;
			                  let NodeArr=this.getDom().textContent;
			                  NodeArr=NodeArr.split(".");
			                  let NodeStr="";
			                  NodeArr.forEach((v,i)=>{
			                      NodeStr+=(v+",");
			                  });
			                  _this.NodeStr=NodeStr;
			                  _this.getListPages();
		    				  _this.handleCurrentChange(1);

			                  // _this.$store.commit('update'+id+'Node',NodeStr);
			                  // _this.$store.dispatch('update'+id,[NodeStr,_this.apiTree.data.ID]);
			                });
			              }else{
			                _this.imgShow=true;
			              }
			            }else{
			              _this.imgShow=true;
			            }
			          })
			          .catch(err => {
			          	 _this.imgShow=true;
			            console.log(err);
			          });
		    	}
		    },
		    getListPages(){
		    	let name=this.NodeStr;
		    	let biaoshi=this.selectArea;
		    	if(biaoshi=="财务法规"||Number(biaoshi)==1){
		    		reqData
				        .req({
				          apiName: "listPages",
				          data: {             
				            name:name,
				            company_name:this.companyName.value
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            this.pages=Number(data);
				          }
				        })
				        .catch(err => {
				          console.log(err);
				          alert('接口500错误')
				       });
		    	}else{
		    		reqData
				        .req({
				          apiName: "zh_listPages",
				          data: {             
				            name:name
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            this.pages=Number(data);
				          }
				        })
				        .catch(err => {
				          console.log(err);
				          alert('接口500错误')
				       });
		    	}
	    	},
	    	handleCurrentChange(val){
	    		let name=this.NodeStr;
	    		let biaoshi=this.selectArea;
	    		if(biaoshi=="财务法规"||Number(biaoshi)==1){
	    			if(name==undefined){
		    			name=this.name;
		    		}
		    		this.name=name;
		    		reqData
				        .req({
				          apiName: "faguiList2",
				          data: {             
				            num:this.currentPage,
				            name:name,
				            company_name:this.companyName.value
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            this.faguiList=data;
				          }
				        })
				        .catch(err => {
				          console.log(err);
				          alert('接口500错误')
				       });
	    		}else{
	    			reqData
			        .req({
			          apiName: "zh_faguiList",
			          data: {             
			            num:this.currentPage,
			            name:name
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.faguiList=data;
			          }
			        })
			        .catch(err => {
			          console.log(err);
			          alert('接口500错误')
			       });
	    		}
	    	},
	    	getFaguiXq(title){
	    		this.faguiXq='null';
	    		this.dialogVisible=true;
	    		let biaoshi=this.selectArea;
	    		if(biaoshi=="财务法规"||Number(biaoshi)==1){
	    			reqData
				        .req({
				          apiName: "faguiXq",
				          data: {             
				            ID:1,
				            icon_name:title
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            let cont=data[0].text;
				    		let reg1=new RegExp("。","g");
				    		let reg3=new RegExp(" ","g");
					    	cont=cont.replace(reg1,"。</p><p>");
							cont=cont.replace(reg3,"</p><p>");
							data[0].text="<p>"+cont+"</p>";
				            this.faguiXq=data;
				          }
				        })
				        .catch(err => {
				          console.log(err);
				          alert('接口500错误')
				       });
	    		}else{
	    			reqData
				        .req({
				          apiName: "zihuanXq",
				          data: {             
				            ID:1,
				            icon_name:title
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				            let cont=data[0].text;
				    		let reg1=new RegExp("。","g");
				    		let reg3=new RegExp(" ","g");
					    	cont=cont.replace(reg1,"。</p><p>");
							cont=cont.replace(reg3,"</p><p>");
							data[0].text="<p>"+cont+"</p>";
				            this.faguiXq=data;
				          }
				        })
				        .catch(err => {
				          console.log(err);
				          alert('接口500错误')
				       });
	    		}
	    	},
	    	unique(arr){
	   　　　 	var newArr = [arr[0].name];
		   		var data=[arr[0]];
		   		//去重复项
		  　　 	for(var i=1;i<arr.length;i++){
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
	    		}
	    		console.log(data);
	    		//区分值和主题
	    		for(var i=0;i<data.length;i++){
	    		 	if(data[i].tag&&data[i].tag==='值'){
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				             }
				         }
				         data[i].symbol='diamond'
	    		 	}
	    		}
	    		console.log(data);
		        return data;
		    },
		    getData(word){
			  	let _this=this;
			  	_this.dialogVisible2=true;
			  	_this.showGrap=false;
			  	_this.showGrap2=false;
			  	_this.showGrap3=false;
			 	 console.log(word);
			 	 word=word?word:'空';
			   	reqData
			        .req({
			          apiName: "Neo4jToJson",
			           data: {             
			             word:word,
			             word1:'南京审计大学'
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);

			            let nodes=data.nodes
						let links=data.links;
						if(nodes.length){
							_this.showGrap=true;
							nodes=_this.unique(nodes);
							console.log(nodes);
							// links=this.linksFormat(links);
							
							var graphCharts = echarts.init(document.getElementById('graphCharts'));
				 	 // 		graphCharts.showLoading();
							// graphCharts.hideLoading();
					    	graphCharts.setOption(_this.graphOption(nodes,links)); 
				    	    $(window).resize(function(){
								graphCharts.resize({
									width:'auto',
									height:'auto'
								})
						    })
						}else{
							alert('南京审计大学无数据');
						}
						

			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			    reqData
			        .req({
			          apiName: "Neo4jToJson",
			           data: {             
			             word:word,
			             word1:'审计署'
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.nodes
						let links=data.links;
						if(nodes.length){
							_this.showGrap2=true;
							nodes=_this.unique(nodes);
							console.log(nodes);
							// links=this.linksFormat(links);
							
							var graphCharts2 = echarts.init(document.getElementById('graphCharts2'));
				 	 // 		graphCharts.showLoading();
							// graphCharts.hideLoading();
					    	graphCharts2.setOption(_this.graphOption(nodes,links));
				    	    $(window).resize(function(){
								graphCharts2.resize({
									width:'auto',
									height:'auto'
								})
						    })
						}else{
							alert('审计署无数据');
						}
						

			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
		        reqData
			        .req({
			          apiName: "Neo4jToJson",
			           data: {             
			             word:word,
			             word1:'审计疑点'
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.nodes
						let links=data.links;
						if(nodes.length){
							_this.showGrap3=true;
							nodes=_this.unique(nodes);
							console.log(nodes);
							// links=this.linksFormat(links);
							
							var graphCharts3 = echarts.init(document.getElementById('graphCharts3'));
				 	 // 		graphCharts.showLoading();
							// graphCharts.hideLoading();
					    	graphCharts3.setOption(_this.graphOption(nodes,links));
				    	    $(window).resize(function(){
								graphCharts3.resize({
									width:'auto',
									height:'auto'
								})
						    })
						}else{
							alert('疑点结论无数据');
						}
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			    reqData
			        .req({
			          apiName: "Neo4jToJson",
			           data: {             
			             word:word,
			             word1:'疑点法规'
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.nodes
						let links=data.links;
						if(nodes.length){
							_this.showGrap4=true;
							nodes=_this.unique(nodes);
							console.log(nodes);
							// links=this.linksFormat(links);
							
							var graphCharts4 = echarts.init(document.getElementById('graphCharts4'));
				 	 // 		graphCharts.showLoading();
							// graphCharts.hideLoading();
					    	graphCharts4.setOption(_this.graphOption(nodes,links));
				    	    $(window).resize(function(){
								graphCharts4.resize({
									width:'auto',
									height:'auto'
								})
						    })
						}else{
							alert('疑点法规无数据');
						}
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
		    },
		    graphOption(nodes,links){
		    	console.log(nodes);
		    	console.log(links)
		    	let option = {
				     title: {
				         text: ''
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     legend: [{
				        data: ['审计疑点', '审计结论']
				     }],
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 48,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 600,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [16, 30],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		    },
		    handleClick(tab, event) {
		        // console.log(tab, event);
				// $(window).resize();
		    }
	    },
	    watch: {
		    selectArea: function (val){
		    	this.tree=false;
		    	this.selectArea=val;
		    	this.NodeStr=',';
	    		this.currentPage=1;
		     	this.getListPages();
    		    this.handleCurrentChange(1);
		    }
		},
	    mounted(){
    		this.getListPages();
    		this.handleCurrentChange(1);
    		
    		//if(sessionStorage.getItem("znjsTree")){
    		//this.tree=true;
    		//var myChart = echarts.init(document.getElementById("searchTree"));
    		//myChart.setOption(this.optionTree(JSON.parse(sessionStorage.getItem("znjsTree"))));
    		//}
	    }

	}
</script>
<style type="text/css" scoped="">
	.cont{
		position: relative;
		text-align: center; 
		margin: 20px auto;
		min-height: 600px;
	    padding: 10px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
	.list-btn{
	    position: absolute;
	    top: 80px;
	    right: 0;
	    z-index: 9;
		width: 32px;
	    text-align: center;
	    background-color: #c24b4b;
	    color: #fff;
	    text-shadow: 5px 2px 6px rgba(0,0,0,0.3);
	    font-size: 16px;
	    padding: 8px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border-radius: 3px;
	    cursor: pointer;
	}
	.tree-box{
		    margin: 20px auto;
		    min-height: 600px;
		    border-radius: 8px;
		    background-color: transparent;
		    /* border: 1px solid rgb( 222, 222, 222 ); */
		    /* -webkit-box-shadow: 0px 1px 3px 0px rgba( 0, 0, 0 ,0.25); */
		    /* box-shadow: 0px 1px 3px 0px rgba( 0, 0, 0 ,0.25); */
		   /* position: fixed;
		    right: 0;
		    bottom: 0;*/
	}
	.cont .item{
	    padding: 20px 0;
	    border-bottom: 1px solid #e0e0e0;
	}
	.cont .item p{
	    height: 28px;
	    line-height: 28px;
	    color: #999999;
	}
	.cont .item .item-head{
	    margin-bottom: 14px;
	    cursor: pointer;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.cont .item .item-head .icon-fl{
	    padding: 7px 12px;
	    background-color: #f59536;
	    border-radius: 5px;
	    color: #fff;
	}
	.cont .item .item-head .title{
	    color: #333;
	    font-size: 18px;
	    /*font-weight: bold;*/
	}
	.cont .item .item-head .title:hover{
		color: #b80000;
	}
	.cont .item .item-b .key{
	    color: #128bed;
	}
	.cont .block{
		text-align: center;
		margin-top: 30px;
		margin-bottom: 80px;
	}
	.cont .min{
		zoom: 0.2;
	}
	/*弹出框法律详情样式结束*/
    .flxq .title{
	    color: #000;
	    font-size: 28px;
	    line-height: 60px;
	    text-align: center;
	}
	.flxq .decs{
	    color: #999999;
	    font-size: 14px;
	    line-height: 26px;
	    margin-bottom: 26px;
	    text-align: center;
	}
	.flxq .decs .key,
	.xwxq .decs .key{
	    font-size: 15px;
	    color: #00a2c9;
	}
	.flxq .cont{
	    color: #333;
	    font-size: 16px;
	    text-align:justify;
	    text-indent: 40px;
	    line-height: 34px;
	}
	.flxq .cont p{
	    margin-bottom: 16px;
	}
	/*弹出框法律详情样式结束*/
	/*层次结构搜索框*/
	.find{
	    margin-top: 14px;
	    /*margin-right: 100px;*/
	}
	.find .lable{
	    color: #00a2c9;
	    font-weight: bold;
	    font-size: 16px;
	}
	.find .search {
	    position: relative;
	    width: 478px;
	    margin: 0 auto;
	    height: 48px;
	    padding-left: 15px;
	    box-sizing: border-box;
	    background-color: #fff;
	    border: 1px solid #999999;
	    border-radius: 6px;

	}

	.find .search input {
	    position: absolute;
	    height: 44px;
	    width: 80%;
	    color: #333;
	    font-size: 16px;
	    outline: none;
	    border: none;
	    background: none;
	}
	.find .search input::-webkit-input-placeholder {
	    color: #ccc;
	    font-size: 16px;
	}

	.find .search input::-moz-placeholder {
	    color: #ccc;
	    font-size: 16px;
	}

	.find .search input::placeholder {
	    color: #ccc;
	    font-size: 16px;
	}

	.find .search button {
	    cursor: pointer;
	    height: 33px;
	    /* top: 20px; */
	    outline: none;
	    margin-top: 7px;
	    margin-right: 4px;
	    border: none;
	    width: 50px;
	    border-left: 1px solid #ccc;
	    background: url(../../../assets/find2.png) no-repeat center;
	}
	.noSjImg{
      display: block; 
      margin: 0 auto;
      padding-top: 140px;
      box-sizing: border-box;
    }
	/*层次结构搜索框结束*/


	.select{
  		padding:5px 10px;
  		border-bottom: #ddd 1px dashed;
	    width: 100%;
	    margin: 20px auto;
  		font-size:12px
  	}
	.select li{
		list-style:none;
		padding:10px 0 5px 100px
	}
	.select .select-list{
		border-bottom:#eee 1px dashed
	}
	.select dl{
		zoom:1;
		position:relative;
		line-height:24px;
	}
	.select dl:after{
		content:" ";
		display:block;
		clear:both;
		height:0;
		overflow:hidden
	}
	.select dt{
		width:100px;
		margin-bottom:5px;
		position:absolute;
		top:0;left:-100px;
		text-align:right;
		color:#666;
		height:24px;
		line-height:24px
	}
	.select dd{
		float:left;
		display:inline;
		margin:0 0 5px 5px;
	}
	.select a{
		display:inline-block;
		white-space:nowrap;
		height:24px;
		padding:0 10px;
		text-decoration:none;
		color:#000;
		border-radius:2px;
	}
	.select a:hover{
		color:#b80000;
		background-color:#fff2f2
	}
	.select .selected a{
		color:#fff;
		background-color:#b80000
	}
	.select-result dt{
		font-weight:bold
	}
	.select-no{
		color:#999
	}
	.select .select-result a{
		padding-right:20px;
		background:#b80000 url("../../../assets/image/page1_2/close.gif") right 9px no-repeat
	}
	.select .select-result a:hoverclose.gif{
		background-position:right -15px
	}
</style>


